    padding: 0;
    margin: 0;
  }
   
  body {
    font-family: 'Poppins', sans-serif;
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
   
   
  /*
    AppBar
  */
   
  .app-bar {
    padding: 8px 16px;
    background-color: white;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    position: sticky;
    top: 0;
    z-index: 99;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  }
   
  .app-bar .app-bar__menu {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__menu button {
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 8px;
    cursor: pointer;
  }
   
  .app-bar .app-bar__brand {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__brand h1 {
    color: #db0000;
    text-transform: uppercase;
    font-size: 22px;
    user-select: none;
  }
   
  .app-bar .app-bar__navigation {
    position: absolute;
    top: 50px;
    left: -180px;
    width: 150px;
    transition: all 0.3s;
    padding: 8px;
    background-color: white;
    overflow: hidden;
  }
   
  .app-bar .app-bar__navigation.open {
    left: 0;
  }
   
  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    text-decoration: none;
    color: black;
    padding: 8px;
    margin-bottom: 5px;
    width: 100%;
  }
   
  /*
    Main Content
  */
   
  main {
  flex-grow: 1;
  align-items: center;
  text-align: center;
  width: 100%;
  }
   
  .content {
    margin: 0 auto;
    max-width: 100%;
    width: 100%;
  }
   
  .content .content__heading {
    font-weight: normal;
  }

  .image-hero img {
    max-width: 100%;
    height: auto;
    
  }


.caption-content {
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
  color: #333; 
  font-weight: bold; 
  line-height: 1.5;
  margin: 75px 0; 
}
   
/* restaurants */

.resto_list{
display: grid;
grid-template-columns: 1fr;
grid-column-gap: 0px;
grid-row-gap: 8px;
padding: 0px 50px;
max-width: 100%;
}
  
   
  /*
    resto Item
  */
   
  .card-item-resto {
    max-width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
    
  }
   
  .card-item_header {
    position: relative;
  }
   
  .card-item-resto .card-item_header .resto-item_header_poster {
    width: 100%;
    width: 100%;
    height: 200px; /* Atur tinggi sesuai dengan kebutuhan Anda */
    object-fit: cover; 
    
  }
   
  .card-item-resto .card-item_header .resto-item_header_rating {
    position: absolute;
    padding: 8px;
    bottom: 20px;
    left: 0;
    display: inline-block;
    background-color: black;
    color: white;
  }
   
  .card-item-resto .card-item_header .resto-item_header_rating .resto-item_header_rating_score {
    margin-left: 10px;
  }

  .card-item-resto .card-item_header .resto-item_header_city {
    position: absolute;
    padding: 8px;
    bottom: 20px;
    right: 0;   
    display: inline-block;
    background-color: black;
    color: white;
  }

  .card-item-resto .card-item_header .resto-item_header_city .resto-item_header_city_name {
    margin-left: 10px;
  }
   
  .card-item-resto .resto-item_content {
    padding: 16px;
  }
   
  .card-item-resto .resto-item_content h3 {
    margin: 0 0 10px 0;
  }
   
  .card-item-resto .resto-item__content h3 a {
    color: #db0000;
    text-decoration: none;
  }
   
  .card-item-resto .resto-item__content p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4; /* number of lines to show */
    -webkit-box-orient: vertical;
    width: 100%;
    text-align: justify;
    white-space: nowrap; 
  }
   
   
