    /* Basic styling for the card container */
.resto_container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

/* Styling for restaurant title */
.resto-title {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

/* Styling for restaurant image */
.restaurant__image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 15px;
}

/* Styling for restaurant information section */
.restaurant__info {
  border-bottom: 1px solid #ccc;
  padding-bottom: 15px;
  margin-bottom: 15px;
}

/* Styling for restaurant information headers */
.restaurant__info h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 10px;
}

/* Styling for restaurant information details */
.restaurant__info h4 {
  font-size: 16px;
  color: #555;
  margin-top: 10px;
}

.restaurant__info p {
  font-size: 18px;
  color: #333;
  margin-bottom: 10px;
}

/* Styling for restaurant overview section */
.restaurant__overview h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 10px;
}

.restaurant__overview p {
  font-size: 18px;
  color: #333;
  line-height: 1.6;
}

/* Styling for restaurant menu section */
.restaurant__menu h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 10px;
}

/* Styling for menu lists */
.menu__foods, .menu__drinks {
  margin-bottom: 15px;
}

.menu__foods h4, .menu__drinks h4 {
  font-size: 18px;
  color: #333;
  margin-bottom: 10px;
}

.menu__foods ul, .menu__drinks ul {
  list-style-type: none;
}

.menu__foods li, .menu__drinks li {
  font-size: 16px;
  color: #555;
}

/* Styling for restaurant reviews section */
.restaurant__reviews h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 15px;
}

/* Styling for customer reviews */
.customer__reviews {
  margin-bottom: 15px;
}

/* Styling for review form */
#reviewForm {
  margin-top: 15px;
}

#reviewForm h4 {
  font-size: 18px;
  color: #333;
  margin-bottom: 10px;
}

.form__group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-size: 16px;
  color: #333;
  margin-bottom: 5px;
}

input,
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  margin-bottom: 10px;
}

button {
  background-color: #4caf50;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}
    /*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}
   
  /*
    Footer
  */
   
  .footer {
    background-color: #ffc0ad;
    color: #271c19;
    text-align: center;
    padding: 20px 0;
    font-family: 'Poppins';
    /* Menambahkan posisi relatif untuk mengatur jarak elemen lain */
  }
  
  .social-icons {
    display: flex;
    justify-content: center; /* Mengatur ikon-ikon berjajar di tengah */
    gap: 10px; /* Mengatur jarak antara ikon-ikon */
    margin-bottom: 10px; /* Menambahkan jarak antara ikon dan teks */
  }
  
  .social-icon {
    font-size: 24px;
    color: #fff;
    text-decoration: none;
    display: flex;
    align-items: center; /* Mengatur ikon-ikon berada di tengah vertikal */
    min-width: 44px; /* Menambahkan dimensi minimal untuk elemen */
    min-height: 44px;
  }
  
  .social-icon img {
    width: 24px; /* Atur ukuran gambar ikon */
    height: 24px;
    margin-right: 5px; /* Mengatur jarak antara ikon dan teks */
  }
  
  .social-icon:hover {
    color: #e78fb3; 
  }
  
  /* Hak Cipta text */
  .footer p {
    margin-bottom: 10px; 
    font-size: 16px;
    font-family: 'Poppins';
  }
  
  
  .social-icon img[alt=""] {
    content: ''; 
    display: block;
    min-width: 44px;
    min-height: 44px;
  }
