max-width: 100%;\r\n}\r\n  \r\n   \r\n  /*\r\n    resto Item\r\n  */\r\n   \r\n  .card-item-resto {\r\n    max-width: 100%;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n    border-radius: 8px;\r\n    overflow: hidden;\r\n    \r\n  }\r\n   \r\n  .card-item_header {\r\n    position: relative;\r\n  }\r\n   \r\n  .card-item-resto .card-item_header .resto-item_header_poster {\r\n    width: 100%;\r\n    width: 100%;\r\n    height: 200px; /* Atur tinggi sesuai dengan kebutuhan Anda */\r\n    object-fit: cover; \r\n    \r\n  }\r\n   \r\n  .card-item-resto .card-item_header .resto-item_header_rating {\r\n    position: absolute;\r\n    padding: 8px;\r\n    bottom: 20px;\r\n    left: 0;\r\n    display: inline-block;\r\n    background-color: black;\r\n    color: white;\r\n  }\r\n   \r\n  .card-item-resto .card-item_header .resto-item_header_rating .resto-item_header_rating_score {\r\n    margin-left: 10px;\r\n  }\r\n\r\n  .card-item-resto .card-item_header .resto-item_header_city {\r\n    position: absolute;\r\n    padding: 8px;\r\n    bottom: 20px;\r\n    right: 0;   \r\n    display: inline-block;\r\n    background-color: black;\r\n    color: white;\r\n  }\r\n\r\n  .card-item-resto .card-item_header .resto-item_header_city .resto-item_header_city_name {\r\n    margin-left: 10px;\r\n  }\r\n   \r\n  .card-item-resto .resto-item_content {\r\n    padding: 16px;\r\n  }\r\n   \r\n  .card-item-resto .resto-item_content h3 {\r\n    margin: 0 0 10px 0;\r\n  }\r\n   \r\n  .card-item-resto .resto-item__content h3 a {\r\n    color: #db0000;\r\n    text-decoration: none;\r\n  }\r\n   \r\n  .card-item-resto .resto-item__content p {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 4; /* number of lines to show */\r\n    -webkit-box-orient: vertical;\r\n    width: 100%;\r\n    text-align: justify;\r\n    white-space: nowrap; \r\n  }\r\n   \r\n   \r\n  /*\r\n    restaurant\r\n  */\r\n   \r\n  /* .restaurant {\r\n    margin: 0 auto;\r\n    width: 100%;\r\n    max-width: 800px;\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 18px 16px;\r\n  }\r\n   \r\n  .restaurant .resto_poster {\r\n    width: 100%;\r\n    max-width: 400px;\r\n  }\r\n   \r\n  .restauarant .movie__info h4 {\r\n    margin: 8px 0;\r\n  }\r\n    */\r\n\r\n    /* Basic styling for the card container */\r\n.resto_container {\r\n  max-width: 800px;\r\n  margin: 20px auto;\r\n  padding: 20px;\r\n  border-radius: 8px;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\r\n  background-color: #fff;\r\n}\r\n\r\n/* Styling for restaurant title */\r\n.resto-title {\r\n  font-size: 24px;\r\n  margin-bottom: 10px;\r\n  color: #333;\r\n}\r\n\r\n/* Styling for restaurant image */\r\n.restaurant__image {\r\n  width: 100%;\r\n  height: auto;\r\n  border-radius: 8px;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n/* Styling for restaurant information section */\r\n.restaurant__info {\r\n  border-bottom: 1px solid #ccc;\r\n  padding-bottom: 15px;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n/* Styling for restaurant information headers */\r\n.restaurant__info h3 {\r\n  font-size: 20px;\r\n  color: #333;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n/* Styling for restaurant information details */\r\n.restaurant__info h4 {\r\n  font-size: 16px;\r\n  color: #555;\r\n  margin-top: 10px;\r\n}\r\n\r\n.restaurant__info p {\r\n  font-size: 18px;\r\n  color: #333;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n/* Styling for restaurant overview section */\r\n.restaurant__overview h3 {\r\n  font-size: 20px;\r\n  color: #333;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.restaurant__overview p {\r\n  font-size: 18px;\r\n  color: #333;\r\n  line-height: 1.6;\r\n}\r\n\r\n/* Styling for restaurant menu section */\r\n.restaurant__menu h3 {\r\n  font-size: 20px;\r\n  color: #333;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n/* Styling for menu lists */\r\n.menu__foods, .menu__drinks {\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.menu__foods h4, .menu__drinks h4 {\r\n  font-size: 18px;\r\n  color: #333;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.menu__foods ul, .menu__drinks ul {\r\n  list-style-type: none;\r\n}\r\n\r\n.menu__foods li, .menu__drinks li {\r\n  font-size: 16px;\r\n  color: #555;\r\n}\r\n\r\n/* Styling for restaurant reviews section */\r\n.restaurant__reviews h3 {\r\n  font-size: 20px;\r\n  color: #333;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n/* Styling for customer reviews */\r\n.customer__reviews {\r\n  margin-bottom: 15px;\r\n}\r\n\r\n/* Styling for review form */\r\n#reviewForm {\r\n  margin-top: 15px;\r\n}\r\n\r\n#reviewForm h4 {\r\n  font-size: 18px;\r\n  color: #333;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.form__group {\r\n  margin-bottom: 15px;\r\n}\r\n\r\nlabel {\r\n  display: block;\r\n  font-size: 16px;\r\n  color: #333;\r\n  margin-bottom: 5px;\r\n}\r\n\r\ninput,\r\ntextarea {\r\n  width: 100%;\r\n  padding: 10px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 4px;\r\n  font-size: 16px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\nbutton {\r\n  background-color: #4caf50;\r\n  color: #fff;\r\n  padding: 10px 15px;\r\n  border: none;\r\n  border-radius: 4px;\r\n  font-size: 16px;\r\n  cursor: pointer;\r\n}\r\n\r\nbutton:hover {\r\n  background-color: #45a049;\r\n}\r\n    /*\r\n  Like\r\n*/\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n  \r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n  \r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n   \r\n  /*\r\n    Footer\r\n  */\r\n   \r\n  .footer {\r\n    background-color: #ffc0ad;\r\n    color: #271c19;\r\n    text-align: center;\r\n    padding: 20px 0;\r\n    font-family: 'Poppins';\r\n    /* Menambahkan posisi relatif untuk mengatur jarak elemen lain */\r\n  }\r\n  \r\n  .social-icons {\r\n    display: flex;\r\n    justify-content: center; /* Mengatur ikon-ikon berjajar di tengah */\r\n    gap: 10px; /* Mengatur jarak antara ikon-ikon */\r\n    margin-bottom: 10px; /* Menambahkan jarak antara ikon dan teks */\r\n  }\r\n  \r\n  .social-icon {\r\n    font-size: 24px;\r\n    color: #fff;\r\n    text-decoration: none;\r\n    display: flex;\r\n    align-items: center; /* Mengatur ikon-ikon berada di tengah vertikal */\r\n    min-width: 44px; /* Menambahkan dimensi minimal untuk elemen */\r\n    min-height: 44px;\r\n  }\r\n  \r\n  .social-icon img {\r\n    width: 24px; /* Atur ukuran gambar ikon */\r\n    height: 24px;\r\n    margin-right: 5px; /* Mengatur jarak antara ikon dan teks */\r\n  }\r\n  \r\n  .social-icon:hover {\r\n    color: #e78fb3; \r\n  }\r\n  \r\n  /* Hak Cipta text */\r\n  .footer p {\r\n    margin-bottom: 10px; \r\n    font-size: 16px;\r\n    font-family: 'Poppins';\r\n  }\r\n  \r\n  \r\n  .social-icon img[alt=\"\"] {\r\n    content: ''; \r\n    display: block;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n  }\r\n  "],"sourceRoot":""}]);
