import FavoriteRestoIdb from '../../data/favorite-resto-idb';
import { templateOfFavorite } from '../templates/template-creator';

const Favorite = {
  async render() {
    return `
    <section class="content">
      <div class="fav_list" id="favList">
       
      </div>
  </section>
      `;
  },

  async afterRender() {
    const fav = await FavoriteRestoIdb.getAllRestaurants();
    const favContainer = document.querySelector('#favList');

    fav.forEach((restaurant) => {
      favContainer.innerHTML += templateOfFavorite(restaurant);
    });
  },
};

export default Favorite;
